html, body {
    margin:0px;
    padding:0px;
    height:100%;
    width:100%;
    font-family:"Merriweather";
    line-height: normal;
    color:$gray;
    height: 100%;
    min-height: 100%;
}

body {
    overflow:visible;
    overflow-y: scroll; /* has to be scroll, not auto */
    -webkit-overflow-scrolling: touch;

}

#site-wrapper {
    display:block;
    width:100%;
    clear:both;
    *zoom:1;
    overflow: visible;
    position:relative;
}

#site-wrapper:before {
    content:'';
    display:table;
}

#site-wrapper:after {
    content:'';
    display:table;
    clear:both;
}

#site-wrapper:before, #site-wrapper:after {
    content:" ";
    clear:both;
    display:block;
}

.section.container {
    position:relative;
}

.icon {
    display:inline-block;
}


